// Medium devices (tablets, 768px and up)
@media (max-width: 768px) {
  .gallery {
    flex-direction: column;
    .gallery__element {
      width: auto;
      margin: 10px auto;
    }
  }
  .row {
    flex-direction: column;
  }
  .counting {
    .counting__item {
      padding-top: 30px;
      padding-bottom: 30px;
      width: 100%;
      border: none;
      border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    }
    .counting__item:last-child {
      border: none;
    }
  }

  .instruments {
    &__row {
      flex-direction: column;
    }
    &__item {
      width: auto;
    }
    &__item_line {
      border: none;
    }
  }

  .service {
    &__img {
      max-width: 100%;
      margin-bottom: 20px;
    }
    .panel-group {
      width: 100%;
    }
  }
  .quote {
    padding-top: 50px;
    padding-bottom: 50px;
    &__icon {
      width: 150px;
      height: 100px;
      padding: 22px;
      border: 3px solid #95e1d3;
      margin-right: 30px;
    }
    &__text q {
      font-size: 14px;
    }
    &__text p {
      font-size: 15px;
    }
  }
}
